.footer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #E1E8ED;
    padding: 5px;
    width: 100%;
    gap: 10px;
    height: 50px;
    border-top: 3px solid #1DA1F2;
    background: #040452;
    opacity: 0.9;
}
.footer #tohome{
    position: absolute;
    background: #f5f8fa;
    width: 48px;
    height: 48px;
    text-align: center;
    border-radius: 6px;
    top: -40px;
    font-size: 36px;
    font-weight: bold;
}
.footer #tohome a{
   color: #1DA1F2;
}

.footer .link a {
    color: #1DA1F2;
}