
.app {
    position: relative;
    top: 0;
    left: 0;
    overflow: hidden;
    letter-spacing: 0.5px;
    background-image: url('./images/bgf3.jpg');
    background-size: cover;
    background-attachment: fixed;
}

.about-skill {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 50px;
    background: #040452;
    border-top: 2px solid #1DA1F2;

}
.app .service-sec {
    position: relative;
    background: #F5F8FA;
    display: flex;
    justify-content: center;
    border-bottom: 3px solid #1DA1F2;
}
