.nav {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 100px;
    transition: all 0.5s ease;
}

#navscroll {
    background: #0a0a58;
    opacity: 0.9;
}

.nav .logo h1{
    margin-left: 30px;
    font-size: 40px;
    font-weight: 900;
    margin-top: 10px;
    color: #1DA1F2 ;
    cursor: default;
}

.nav .logo h1 #sp{
    font-size: 50px;
    font-weight: 900;
    color: #E1E8ED;
}

.nav .menu .lists {
    margin-right: 50px;
    display: flex;
    justify-content: center;
    gap: 30px;
}

.nav .menu .lists  .links , .nav .menumobile .lists a{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 26px;
    font-weight: 900;
    color: #1DA1F2 ;
    padding: 3px 10px;
}

.nav .menumobile .lists .alink{
    border-left: 5px solid #1DA1F2;
    padding: 10px;
    width: 100%;
}

.nav .menu .lists .links:hover{
    color: #E1E8ED;
}
nav .menu .social .linksocial:hover {
    color: #1DA1F2;
}

/* responsive menu */

.nav .hidden {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    align-content: center;
    background: #0a0a58;
    opacity: 0.9;
    clip-path: circle(100px at 90% -15%);
    -webkit-clip-path: circle(100px at 90% -15%) ;
    transition: all 2s ease-out;
}

.nav .hidden.menumobile {
    clip-path: circle(800px at 90% -10%);
    -webkit-clip-path: circle(800px at 90% -10%) ;
}

.nav .bar {
    position: fixed;
    top: 12px;
    right: 20px;
    color: #1DA1F2;
    font-size: 35px;
    font-weight: 300;
    cursor: pointer;
}

.nav .nobar {
    visibility: hidden;
}

.nav .menumobile .lists {
    display: flex;
    margin-top: 60px;
    margin-left: -15px;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    gap: 30px;
}

.nav .menumobile .toggle {
    position: fixed;
    padding: 0px;
    top: 10px;
    right: 20px;
    font-size: 35px;
    font-weight: 600;
    color: #1DA1F2;
    cursor: pointer;
}

.nav .menu .lists .social, .nav .menumobile .lists .social{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.nav .menu .lists .social a {
    padding: 2px 5px;
    color: #E1E8ED;
    font-size: 19px;
}
.nav .menu .lists .social a:hover {
    color: #1DA1F2;
}

 .nav .menumobile .lists .social a{
    color: #E1E8ED;
    font-size: 22px;
}

.nav .menu .lists button a {
    color: #333;
}

.nav .menumobile .lists button a {
    color: #E1E8ED;
}

.nav button {
    font-size: 18px;
    font-weight: bold;
    background: #1DA1F2;
    padding: 8px 16px;
}

.nav .menumobile button {
   background: #1DA1F2;
}

/* for responsive */
@media screen and (max-width: 1250px) {
    .nav {
        justify-content: space-between;
    }
}
@media screen and (max-width: 950px) {
    .nav .logo h1{
        font-size: 36px;
        font-weight: 900;
        margin-top: -3px;
        color: #1DA1F2 ;
    }
    .nav .logo h1 span{
        font-size: 48px;
        font-weight: 900;
        
    }
  }